// Template for interactive stories
const Template = (args: any) => {
  const [value, setValue] = useState(args.value || args.defaultValue || '');

  return (
    <div style={{padding: '20px', maxWidth: '600px', width: '100%'}}>
      <FormField
        {...args}
        value={args.type !== 'textarea' ? value : undefined}
        defaultValue={args.type === 'textarea' ? value : undefined}
        onChange={(e) => {
          // Support both patterns: event handler or direct value
          const newValue =
            typeof e === 'string'
              ? e
              : (e.target as HTMLInputElement | HTMLTextAreaElement).value;
          setValue(newValue);
          action('onChange')(newValue);
        }}
        onFocus={action('onFocus')}
        onBlur={(e) => {
          // Support both patterns: event handler or direct value
          const blurValue =
            typeof e === 'string'
              ? e
              : (e.target as HTMLInputElement | HTMLTextAreaElement).value;
          action('onBlur')(blurValue);
        }}
      />
    </div>
  );
};

