// Interactive playground
export const InteractivePlayground: Story = {
  render: (args) => {
    const [value, setValue] = useState(args.value || '');
    const [events, setEvents] = useState<string[]>([]);

    const addEvent = (eventName: string, data?: any) => {
      const timestamp = new Date().toLocaleTimeString();
      setEvents((prev) => [
        `${timestamp}: ${eventName}${data ? ` - ${JSON.stringify(data)}` : ''}`,
        ...prev.slice(0, 9), // Keep last 10 events
      ]);
      action(eventName)(data || {});
    };

    return (
      <div style={{padding: '20px', maxWidth: '800px'}}>
        <div
          style={{
            marginBottom: '20px',
            padding: '15px',
            backgroundColor: '#e3f2fd',
            borderRadius: '8px',
          }}
        >
          <h3 style={{margin: '0 0 10px 0', color: '#1976d2'}}>
            Interactive Playground
          </h3>
          <p style={{margin: '0', fontSize: '14px', color: '#424242'}}>
            Try interacting with the field below and see events in real-time.
          </p>
        </div>

        <FormField
          {...args}
          value={args.type !== 'textarea' ? value : undefined}
          defaultValue={args.type === 'textarea' ? value : undefined}
          onChange={(e) => {
            // Support both patterns: event handler or direct value
            const newValue = typeof e === 'string' ? e : e.target.value;
            setValue(newValue);
            addEvent('onChange', newValue);
          }}
          onFocus={(e) => addEvent('onFocus')}
          onBlur={(e) => {
            // Support both patterns: event handler or direct value
            const blurValue = typeof e === 'string' ? e : e.target.value;
            addEvent('onBlur', blurValue);
          }}
          onKeyDown={(e) => addEvent('onKeyDown', e.key)}
          onKeyUp={(e) => addEvent('onKeyUp', e.key)}
        />

        <div
          style={{
            marginTop: '20px',
            padding: '15px',
            backgroundColor: '#f5f5f5',
            borderRadius: '8px',
            maxHeight: '200px',
            overflowY: 'auto',
          }}
        >
          <h4 style={{margin: '0 0 10px 0', fontSize: '14px'}}>Event Log:</h4>
          {events.length > 0 ? (
            <ul
              style={{
                margin: 0,
                paddingLeft: '20px',
                fontSize: '12px',
                color: '#666',
              }}
            >
              {events.map((event, index) => (
                <li key={index}>{event}</li>
              ))}
            </ul>
          ) : (
            <p style={{margin: 0, fontSize: '12px', color: '#999'}}>
              No events yet. Try typing, focusing, or blurring the field.
            </p>
          )}
        </div>
      </div>
    );
  },
  args: {
    label: 'Interactive Field',
    type: 'text',
    placeholder: 'Try typing, focusing, and blurring...',
  },
  parameters: {
    docs: {
      description: {
        story:
          'Interactive playground with live event logging. See all events fired by the component.',
      },
    },
  },
};

